<template>
  <div>
    <auth-form-template-component
      v-if="showsRegister"
      title="新規登録"
      submitTo="/register"
      linkTo="ログイン"
      @link="link"/>

    <auth-form-template-component
      v-if="!showsRegister"
      title="ログイン"
      submitTo="/login"
      linkTo="新規登録"
      @link="link"/>
  </div>
</template>

<script>
import { defineComponent } from "@vue/composition-api";
import { ref } from "vue"
import AuthFormTemplateComponent from "../templates/AuthFormTemplateComponent.vue";

export default defineComponent({
  components: { AuthFormTemplateComponent },
  setup() {
    const showsRegister = ref(true)

    const link = () => {
      if(showsRegister.value){
        showsRegister.value = false
      }else {
        showsRegister.value = true
      }
    }

    return {
      showsRegister,
      link,
    }
  },
})
</script>
