<template>
  <auth-view-component v-on:login="login" v-if="!isLoggingIn"/>
  <home-template v-if="isLoggingIn"></home-template>
</template>

<script>
import { defineComponent } from "@vue/composition-api";
import axios from "axios"
import AuthViewComponent from "./components/views/AuthViewComponent.vue";

export default defineComponent({
  components: { AuthViewComponent },
  setup() {
    let isLoggingIn = false

    const login = () => {
      axios.get('/login').then(res => {
        if(res != null) {
          isLoggingIn = true;
        }
      })
    }

    return {
      isLoggingIn,
      login,
    }
  },
})
</script>
